.notes-container {
    width: 300px;
    margin: 20px auto;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .notes-container h2 {
    margin-bottom: 10px;
    font-size: 18px;
     
  }
  
  textarea {
    width: 90%;
    height: 150px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
    font-size: 14px;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
  }
  
  .clear-button,
  .save-button {
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
  }
  
  
  
  .save-button {
    background-color: #000900;
    color: white;
  }
  
  
  
  .save-button:hover {
    background-color: #070807;
  }
  